.stats-container {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  margin-top: 10px;
  background-color: rgba(193, 153, 225, 0.656);
  border-radius: 15px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.rate-section, .budget-section {
  flex: 1;
  margin: 0 10px;
}

.rate-bar-container {
  margin-bottom: 10px;
}

.rate-bar-container span {
  margin-right: 10px;
}

.rate-bar-background {
  background-color: #ddd; /* 背景色を薄い灰色に設定 */
  width: 100%; /* 背景の長さを最大に */
  height: 20px;
}

.rate-bar {
  height: 100%;
  transition: width 0.5s ease; /* 棒グラフの幅の変更にアニメーションを追加 */
}

.budget-bar-container img {
  max-width: 100%; /* 画像がコンテナを超えないように */
  height: auto; /* アスペクト比を保持 */
  transition: width 0.5s ease; /* サイズ変更のアニメーション */
}

/* 追加のスタイルが必要な場合はここに書く */
/* スマートフォンサイズ (480px以下) のスタイル */
@media (max-width: 480px) {
  .stats-container {
    flex-direction: row; /* アイテムを横に並べる */
    flex-wrap: wrap; /* コンテンツが多い場合は折り返す */
    align-items: flex-start; /* アイテムをコンテナの上端に揃える */
    padding: 10px; /* パディングを調整 */
    margin-top: 10px; /* 上の余白を調整 */
  }

  .rate-section, .budget-section {
    width: 50%; /* 幅を50%に設定して、2つが横に並ぶようにする */
    margin: 0; /* 余白をリセット */
  }

  .rate-bar-container, .budget-bar-container {
    width: 100%; /* バーコンテナの幅を100%に設定 */
  }

  .rate-bar-container span, .budget-section span {
    display: block; /* ブロック要素として表示 */
    font-size: 14px; /* フォントサイズを適切な大きさに調整 */
  }

  .rate-section h2, .budget-section h2 {
    font-size: 16px; /* セクション見出しのフォントサイズを小さく調整 */
    margin-bottom: 5px; /* 見出し下の余白を調整 */
  }

  .rate-bar-background, .budget-bar-container {
    height: 10px; /* バーの高さを調整 */
  }

  /* 予算の画像のスタイルを調整 */
  .budget-section h2 {
    margin-bottom: 10px; /* タイトル下の余白 */
  }
  
  .budget-bar-container {
    margin-bottom: 10px; /* 画像下の余白 */
  }
  
  .budget-bar-container img {
    display: block; /* 画像をブロック要素にして余白を適用 */
    max-width: 50%; /* 画像がコンテナを超えないように */
    width: auto; /* 画像の幅を自動調整に */
    height: auto; /* アスペクト比を保持 */
    transition: width 0.5s ease; /* サイズ変更のアニメーション */
    margin-bottom: 20px; /* 画像とテキストの間のマージンを設定 */
  }
  
}
